<template>
    <div>
        <el-container>
            <el-header>
                上传SAR影像
            </el-header>
            <el-main>
                <el-row :gutter="15">
                    <el-col :span="18" :class="{'col-17-step2':step===2}">
                        <el-card shadow="hover" v-show="step===1">
                            <el-form label-position="right" label-width="20%">
                                <el-form-item label="水库名称">
                                    <el-select v-model="form.selectedReservoir"
                                               placeholder="请选择">
                                        <el-option
                                                v-for="item in reservoirList"
                                                :label="item.name"
                                                :value="item.name"
                                                :key="item.id">
                                            {{item.name}}
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="卫星名称">
                                    <el-input v-model="form.satelliteName" placeholder="请输入卫星名称"
                                              style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="卫星周期">
                                    <el-input v-model="form.cycle" placeholder="请输入周期" style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="日期">
                                    <el-date-picker type="date" v-model="form.date"
                                                    placeholder="请输入日期"></el-date-picker>
                                </el-form-item>
                                <el-form-item label="分割算法">
                                    <el-select v-model="form.cutAlgo"
                                               placeholder="请选择">
                                        <el-option
                                                v-for="item in cutAlgoList"
                                                :label="item.name"
                                                :value="item.name"
                                                :key="item.id">
                                            {{item.name}}
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="左上角经纬度">
                                    <el-input v-model="form.topLeft" placeholder='以空格分隔经纬度，例如"111.01 32.97"'
                                              style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="右下角经纬度">
                                    <el-input v-model="form.lowerRight" placeholder='以空格分隔经纬度，例如"111.75 32.93"'
                                              style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="选择图像文件">
                                    <el-upload
                                            class="upload-drag-list"
                                            drag
                                            action=""
                                            :limit="1"
                                            :auto-upload="false"
                                            :show-file-list="true"
                                            :on-change="handleFileChanges"
                                            :on-remove="handleFileChanges"
                                            :multiple="false"
                                            :file-list="fileList"
                                            list-type="picture">
                                        <i class="el-icon-upload"></i>
                                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                                    </el-upload>
                                </el-form-item>
                                <el-button @click="stepOneClickNext"
                                           :disabled="stepOneClickNextDisable"
                                           v-text="stepOneNextText">
                                </el-button>
                            </el-form>
                        </el-card>
                        <el-card shadow="hover"
                                 class="el-card-step2"
                                 v-show="step===2"
                                 :body-style="{
                                     flex:'1',
                                     display:'flex',
                                     flexDirection:'column',
                                     padding:'0',
                                 }">
                            <div style="flex:1; position: relative">
                                <el-image src="/upload/sar/getImageData?type=o"
                                          fit="contain"
                                          class="el-image-step2"
                                          v-if="step===2"
                                />
                                <el-image src="/upload/sar/getImageData?type=n"
                                          fit="contain"
                                          class="el-image-n el-image-step2"
                                          v-if="step===2"
                                          :style="{'opacity': 0.01 * opacity}"
                                />
                            </div>

                            <div :style="{
                                display: 'flex',
                                alignItems: 'center',
                                margin:'10px',
                                flexBasis: '40px'
                            }">
                                <span :style="{'opacity': 1- (0.01 * opacity) * 0.8, transition: '.1s'}">处理前</span>
                                <div style="flex:1; margin-left: 20px; margin-right: 20px">
                                    <el-slider v-model="opacity" :show-tooltip="false"></el-slider>
                                </div>
                                <span :style="{'opacity': (0.01 * opacity) * 0.8 + 0.2, transition: '.1s'}">处理后</span>
                                <div style="flex:5">
                                    <el-button style="margin-bottom: 0" @click="stepTwoClickNext">
                                        下一步
                                    </el-button>
                                    <el-button style="margin-bottom: 0" @click="stepTwoClickBack">
                                        上一步
                                    </el-button>
                                </div>

                            </div>
                        </el-card>
                        <el-card shadow="hover" v-show="step===3">
                            <el-row style="display: flex;">
                                <el-col :span="4">
                                    <el-image src="/upload/sar/getImageData?type=n"
                                              fit="contain"
                                              :preview-src-list="['/upload/sar/getImageData?type=n']"
                                              v-if="step===3"
                                              style="border: dotted #909399;"
                                    />
                                </el-col>
                                <el-col :span="20" style="flex: 1; display: flex; align-items: center; margin-left: 1em">
                                    从图像中提取出的水域面积为：
                                    <span style="color: #00b776" v-text="waterArea">
                                    </span>
                                </el-col>
                            </el-row>
                            <el-button type="success" @click="stepThreeClickSave">
                                确认并保存
                            </el-button>
                            <el-button @click="stepTwoClickBack">
                                上一步
                            </el-button>
                        </el-card>
                        <el-card shadow="hover" v-show="step===4">
                            <i class="el-icon-success"></i>
                            数据已保存
                            <el-button @click="stepFourClickWatchData">
                                查看数据
                            </el-button>
                        </el-card>
                    </el-col>

                    <el-col :span="6">
                        <el-card shadow="hover">
                            <el-steps direction="vertical" :active="step-1">
                                <el-step title="上传文件" description="输入信息，上传图像文件"></el-step>
                                <el-step title="确认图像处理结果" description="检查图像是否被正确处理"></el-step>
                                <el-step title="水域面积提取" description="确认水域面积提取结果"></el-step>
                                <el-step title="上传完成"></el-step>
                            </el-steps>
                        </el-card>
                    </el-col>


                </el-row>


            </el-main>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "uploadSarImage",
        data: function () {
            return {
                fileList: [],
                reservoirList: [],
                cutAlgoList: [
                    {
                        id: 1,
                        name: "FCM",
                    },
                ],
                form: {
                    selectedReservoir: '',
                    satelliteName: '',
                    cycle: '',
                    date: '',
                    cutAlgo: '',
                    topLeft: '111.014579 33.037909',
                    lowerRight: '111.75507 32.556198',

                },
                selectRadar: {
                    index: '',
                    lng: '',
                    lat: '',
                    level: '',
                },
                uploadPercentage: 0,
                resultList: [],
                step: 1,
                pointer: {
                    url: '/marker.png',
                    size: {width: 50, height: 50},
                },
                centerPoint: {
                    lng: '',
                    lat: '',
                },
                zoom: 15,
                opacity: 100,
                stepOneNextText: "下一步",
                uploading: false,
                waterArea:'',
            }
        },
        computed: {
            stepOneClickNextDisable() {
                return this.form.selectedReservoir === ''
                    || this.form.satelliteName === ''
                    || this.form.cycle === ''
                    || this.form.date === ''
                    || this.form.cutAlgo === ''
                    || this.form.topLeft === ''
                    || this.form.lowerRight === ''
                    || this.fileList.length === 0
                    || this.uploading;
            },

        },
        methods: {
            handleFileChanges: function (file, fileList) {
                this.fileList = fileList;
            },
            stepOneClickNext: function () {
                //check
                this.uploading = true;
                this.stepOneNextText = "正在处理数据";
                let formData = new FormData();
                for (let i = 0; i < this.fileList.length; i++) {
                    formData.append('sarFile', this.fileList[i].raw)
                }
                this.axios({
                    url: "/upload/sar/sarFile",
                    method: "post",
                    data: formData,
                    headers: {
                        "Content-Type": "multipart/form-data;charset=utf-8"
                    },
                    onUploadProgress: progressEvent => {
                        this.uploadPercentage = (progressEvent.loaded / progressEvent.total * 100 | 0);
                    }
                }).then((response) => {
                    console.log(response);
                    if (response.status === 200) {
                        this.step++;
                        this.stepOneNextText = "下一步";
                        this.uploading = false;
                    } else {
                        console.log("error")
                    }
                })
            },
            stepTwoClickBack: function () {
                this.step--;
            },
            stepTwoClickNext: function () {
                let formData = new FormData();
                formData.append('reservoirName', this.form.selectedReservoir);
                formData.append('satelliteName', this.form.satelliteName);
                formData.append('cycle', this.form.cycle);
                formData.append('date', this.form.date);
                formData.append('cutAlgo', this.form.cutAlgo);
                formData.append('topLeft', this.form.topLeft);
                formData.append('lowerRight', this.form.lowerRight);
                this.axios.post('/upload/sar/getArea', formData).then((response)=>{
                    if (response.status===200){
                        this.waterArea = response.data;
                        this.step++;
                    } else {
                        console.log("error")
                    }
                })
            },
            stepThreeClickSave: function(){
                let formData = new FormData();
                formData.append('reservoirName', this.form.selectedReservoir);
                formData.append('date', this.form.date);
                formData.append('cutAlgo', this.form.cutAlgo);
                this.axios.post('/upload/sar/saveArea', formData).then((response)=>{
                    if (response.status===200){
                        this.waterArea = response.data;
                        this.step++;
                    } else {
                        console.log("error")
                    }
                })
            },
            stepFourClickWatchData: function () {
                this.$router.push('/dm')

            }

        },
        mounted() {
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;

                } else {
                    console.log("error")
                }
            })
        }
    }
</script>

<style scoped>
    .el-container {
        min-height: 100%;
    }

    .el-main {
        height: 100%;
        display: inline-flex;
        padding: 0 5px 0 20px;
    }

    .el-row {
        width: 100%;
        overflow: hidden;
    }

    .el-col-18 {
        height: 100%;
        overflow-y: auto;
    }

    .el-col-18::-webkit-scrollbar {
        display: none;
    }

    .col-17-step2 {
        display: flex;
    }

    .el-progress {
        float: right;
        width: 50%;
        margin-top: 10px;
    }

    .el-button {
        float: right;
        margin-bottom: 20px;
        margin-left: 20px;
    }

    .el-table {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .el-image-step2 {
        margin: 10px 10px 0 10px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    .el-image-n {
        transition: .1s;
    }

    .el-card {
        border: 1px solid #d4d4d4;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .el-card-step2 {
        flex: 1;
        display: flex;
    }

    .upload-drag-list {
        max-width: 70%;
    }

    .el-steps {
        height: 250px;
    }

    .radar-map {
        flex: 1;
    }

    .el-card-step2-span {
        color: #00b776;
    }
</style>
